<template>
  <div class="stay">
    <div>
      <el-button type="info" round @click="logout">logout</el-button>
    </div>
    <h1>待機室</h1>
    <br>
    現在(１){{this.pe}}人待ちです。
    <br>
  </div>
</template>

<script>
  import firebase from 'firebase'
  import axios from "axios"

  export default {
    data() {
      return {
        username: this.$_getusername(),
        pe:0
      }
    },
    async mounted(){
      this.pe = await axios.get('https://script.google.com/macros/s/AKfycbwXs6H8-PbIn-NGqYbYMcqF6I0Omwhq55GWixC-dJuR5uRuF_S9srlCaUXF8vE9UEFSxw/exec?address='+ 'test@test.com' +'&num=1').data.i;

      this.timeout()
    },
    methods: { 
    
      logout: function(){
        this.gasLogout();
        this.logouotRequest(this.email)
        firebase.auth().signOut();
        this.$router.push('/')
      },
      logouotRequest: async function(username){
        const params = new URLSearchParams();
        params.append("name",username);
        await axios.post("http://localhost:8040/logout", params)
        alert("ログアウトしました")
      },
      async gasLogout(){
        await axios.get('https://script.google.com/macros/s/AKfycbwXs6H8-PbIn-NGqYbYMcqF6I0Omwhq55GWixC-dJuR5uRuF_S9srlCaUXF8vE9UEFSxw/exec?address='+ 'test@test.com' +'&num=2');
      },
      async gasUpdate(){
        this.pe = await axios.get('https://script.google.com/macros/s/AKfycbwXs6H8-PbIn-NGqYbYMcqF6I0Omwhq55GWixC-dJuR5uRuF_S9srlCaUXF8vE9UEFSxw/exec?address='+ 'test@test.com' +'&num=1').data;
      },
      timeout: function(){
        this.timeoutTimer = setTimeout(() => {
          this.gasUpdate();
          clearTimeout(this.timeoutTimer)
          }
          ,600000 //時間変更可能 600000
        )
      }
    }
  }
</script>
